import React from "react";
import Layout from "./../components/Layout/Layout";
import SimpleCard from "./../components/SimpleCard/SimpleCard";
import {
  Table,
  TableRow,
  TableHead,
  TableHeader,
  TableBody,
  TableCell,
  Button,
  Loading,
} from "carbon-components-react";

import { useParams } from "react-router-dom";
import { useHistory } from "react-router-dom";

import { connect } from "react-redux";
import { useFirestoreConnect } from "react-redux-firebase";

import { TrashCan16 } from "@carbon/icons-react";

function ConnectionStatus(props) {
  const { id } = useParams();
  const history = useHistory();

  const uid = props.uid;

  useFirestoreConnect([
    {
      collection: "user",
      doc: uid,
      subcollections: [{ collection: "credentials" }],
      storeAs: "connections",
    },
  ]);

  const connection = props.connections.find((elm) => {
    return elm.id === id;
  });

  const deleteConnection = () => {
    fetch(
      "https://us-central1-connectivitys-2066c.cloudfunctions.net/api/deleteConnection/",
      {
        method: "POST", // or 'PUT'
        body: JSON.stringify({
          uid: props.uid,
          connectionName: connection.id,
          token: props.token,
        }), // data can be `string` or {object}!
        headers: {
          "Content-Type": "application/json",
        },
      }
    )
      .then((res) => {
        return res.json();
      })
      .then(history.push("/app/dashboard"))
      .catch(() => {
        history.push("/app/dashboard");
      });
  };

  if (connection.user) {
    return (
      <div className="connection-status">
        <SimpleCard title={connection.id}>
          <Layout
            col={2}
            first={
              <Table>
                <TableHead>
                  <TableHeader>
                    <h3>Connection's Info</h3>
                  </TableHeader>
                </TableHead>

                <TableBody>
                  <TableRow>
                    <TableCell>Name: {connection.user.name}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>JID: {connection.user.jid}</TableCell>
                  </TableRow>
                </TableBody>

                <TableHead>
                  <TableHeader>
                    <h3>Device Info</h3>
                  </TableHeader>
                </TableHead>

                <TableBody>
                  <TableRow>
                    <TableCell>
                      Device manufacurer:
                      {connection.user.phone.device_manufacturer}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>
                      Device model: {connection.user.phone.device_model}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>MCC: {connection.user.phone.mcc}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>MNC: {connection.user.phone.mnc}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>
                      OS build number: {connection.user.phone.os_build_number}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>
                      OS version: {connection.user.phone.os_version}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>
                      WA version: {connection.user.phone.wa_version}
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            }
            second={
              <img
                src={`${connection.user.imgUrl}`}
                style={{ width: "100%" }}
                alt="Profile associated with this wa account"
              />
            }
          />
        </SimpleCard>
        <Button
          kind="danger"
          renderIcon={TrashCan16}
          onClick={deleteConnection}
        >
          Delete connection
        </Button>
      </div>
    );
  } else {
    return <Loading />;
  }
}

const stateToProps = (state) => {
  return {
    uid: state.firebase.auth.uid,
    connections: state.firestore.ordered.connections,
    token: state.firebase.profile.token.token,
  };
};

export default connect(stateToProps)(ConnectionStatus);
