import React from "react";
import Layout from "./../components/Layout/Layout";
import SimpleCard from "./../components/SimpleCard/SimpleCard";
import {
  Table,
  TableRow,
  TableHead,
  TableHeader,
  TableBody,
  TableCell,
  Button,
} from "carbon-components-react";

import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";
import { connect } from "react-redux";
import { useFirestoreConnect } from "react-redux-firebase";
import { TrashCan16 } from "@carbon/icons-react";

function ContactStatus(props) {
  const { id } = useParams();
  const uid = props.uid;

  const history = useHistory();

  const deleteContact = () => {
    fetch(
      "https://us-central1-connectivitys-2066c.cloudfunctions.net/api/deleteContact/",
      {
        method: "POST",
        body: JSON.stringify({
          uid: props.uid,
          contactName: id,
          token: props.token,
        }),
        headers: { "Content-Type": "application/json" },
      }
    )
      .then((res) => {
        return res.json();
      })
      .then(history.push("/app/dashboard"))
      .catch(() => {
        history.push("/app/dashboard");
      });
  };

  useFirestoreConnect([
    {
      collection: "user",
      doc: uid,
      subcollections: [{ collection: "contacts" }],
      storeAs: "contacts",
    },
  ]);

  const contact = props.contacts.find((elm) => {
    return elm.id === id;
  });

  return (
    <div className="contact-status">
      <SimpleCard title={contact.id}>
        <Layout
          col={2}
          first={
            <div>
              <p>
                This is a list with all your contacts available variables. To
                call them in the message, just type its name inside curly
                braces.
              </p>
              <br />
              <br />
              <p>
                If you call a variable that doesnt exists, undefined is going
                to appear on its place in the message.
              </p>
            </div>
          }
          second={
            <Table>
              <TableHead>
                <TableHeader>
                  <h3>Available variables</h3>
                </TableHeader>
              </TableHead>

              <TableBody>
                {contact.variables.map((elm, i) => {
                  return (
                    <TableRow key={i}>
                      <TableCell> {`{ ${elm} }`}</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>

              <TableHead>
                <TableHeader>
                  <h3>Contacts Available</h3>
                </TableHeader>
              </TableHead>

              <TableBody>
                <TableRow>
                  <TableCell> {contact.length} contacts </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          }
        />
        <Button onClick={deleteContact} kind="danger" renderIcon={TrashCan16}>
          Delete contact list
        </Button>
      </SimpleCard>
    </div>
  );
}

const stateToProps = (state) => {
  return {
    uid: state.firebase.auth.uid,
    contacts: state.firestore.ordered.contacts,
    token: state.firebase.profile.token.token,
  };
};

export default connect(stateToProps)(ContactStatus);
